import { ChevronLeftIcon } from "@chakra-ui/icons";
import {
  Center,
  Flex,
  Grid,
  Input,
  InputGroup,
  Radio,
  RadioGroup,
  Textarea,
} from "@chakra-ui/react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import { useAppSelector } from "src/hooks/useStore";
import { globalSelector } from "../../store/global";

const typeRp = ["Fake", "Scam", "Malware", "Prohibited", "Phishing", "Other"];

export default function Report() {
  const { searchKey } = useAppSelector(globalSelector);
  const [url, setUrl] = useState<string>(searchKey);
  const [description, setDescription] = useState<string>("");
  const [type, setType] = useState<string>();
  const history = useHistory();

  return (
    <Flex direction="column" position="relative">
      <ChevronLeftIcon
        position="absolute"
        boxSize={8}
        top={3}
        left={2}
        cursor="pointer"
        onClick={() => {
          history.goBack();
        }}
      />
      <Center color="primary.200" fontWeight="semibold" mt={3} fontSize="xl">
        Report this website
      </Center>
      <InputGroup>
        <Input
          background="#F7F7F7"
          value={url}
          onChange={(e) => {
            setUrl(e.target.value);
          }}
          mx={4}
          mt={4}
          placeholder="Insert URL to report"
        />
      </InputGroup>
      <RadioGroup onChange={setType} value={type}>
        <Grid gridTemplateColumns="repeat(2,1fr)" mx={4}>
          {typeRp.map((item) => (
            <Radio
              value={item}
              _focus={{
                boxShadow: "none",
                outline: "none",
              }}
              size="lg"
              mt={2}
            >
              {item}
            </Radio>
          ))}
        </Grid>
      </RadioGroup>
      <Textarea
        value={description}
        onChange={(e) => {
          setDescription(e.target.value);
        }}
        placeholder="Tell us why you consider this website is a phishing website"
        size="sm"
        background="#F7F7F7"
      />
    </Flex>
  );
}
